import React from 'react'

const Navbar = ({filterItem, menuList}) => {
  return (
    <>
     <nav className="navbar">
      <div className="button-group">
        {menuList.map((curEle) => {
          return(
            <button className="item" onClick={() => filterItem(curEle)}>{curEle}</button>
          );
        })}
        {/* <button className="item" onClick={() => filterItem("Breakfast")}>Breakfast</button>
        <button className="item" onClick={() => filterItem("Lunch")}>Lunch</button>
        <button className="item" onClick={() => filterItem("Dinner")}>Dinner</button> */}
        {/* <button className="item" onClick={() => setMenuData(Menu)}>All</button> */}

      </div>
    </nav>
    </>
  )
}

export default Navbar
