import React from 'react'

const MenuCard = ({ menuData }) => {
  return(
  <>
  <div  className="section">
     {menuData.map((currEle) => {

       //destructuring that will save us to repeat the currEle
       const {id, name, price, category, image, description} = currEle;
       return (
         <>
           <div className="card-container" key={id}>
             <div className="card">
               <div className="card-body">
                 <span className="card-number card-circle subtle">{id}</span>
                 <div className="subtleoftale">
                   <div
                     className="card-author"
                     style={{
                       color: "black",
                       fontSize: "14px",
                       fontWeight: "bold",
                       textTransform: "uppercase",
                     }}
                   >
                     {category}
                   </div>
                   <div className="heading">
                     <h2>{name}</h2>
                   </div>
                 </div>

                 <div className="card-description">
                   <p>{description}</p>
                 </div>
                 <hr />
                 <div className="imageBox">
                   <img src={image} alt="image" className="card-image" />
                 </div>
                 <div className="subtleoftale">
                   <div className="tag-price">{price}</div>
                   <div className="tag-name"><a href="https://www.zomato.com/" target="_blank"> Order Now</a></div>
                 </div>
               </div>
             </div>
           </div>
         </>
       );
     })}
     </div>
  </>
  )

}

export default MenuCard
